body {
  /* fonts */
  --wpp-font-family: 'Inter', 'Roboto', sans-serif;

  /* color palette */

  /* greys */
  --wpp-grey-color-000: #FFFFFF;
  --wpp-grey-color-100: #F8F9FB;
  --wpp-grey-color-200: #F4F5F7;
  --wpp-grey-color-300: #E7EAEE;
  --wpp-grey-color-400: #C1C7CD;
  --wpp-grey-color-500: #A2A9B0;
  --wpp-grey-color-600: #8B919A;
  --wpp-grey-color-700: #697077;
  --wpp-grey-color-800: #4D5358;
  --wpp-grey-color-900: #343A3F;
  --wpp-grey-color-1000: #121619;

  /* primary */
  --wpp-primary-color-100: #EEE8FF;
  --wpp-primary-color-200: #DBD2FA;
  --wpp-primary-color-300: #CAB8FF;
  --wpp-primary-color-400: #8508E8;
  --wpp-primary-color-500: #5E00B5;
  --wpp-primary-color-600: #47039B;
  --wpp-primary-color-700: #2F0069;
  --wpp-primary-color-800: #0B001C;

  /* system colors */
  --wpp-danger-color-100: rgba(255, 236, 233, 0.8);
  --wpp-danger-color-200: #FBE4E4;
  --wpp-danger-color-300: #F7CACA;
  --wpp-danger-color-400: #DF2F2F;
  --wpp-danger-color-500: #B91C1C;
  --wpp-danger-color-600: #921616;

  --wpp-warning-color-200: #FDEAE2;
  --wpp-warning-color-400: #F15619;
  --wpp-warning-color-500: #C2410C;

  --wpp-highlight-color-200: rgba(251, 241, 188, 0.4);
  --wpp-highlight-color-400: #F5DB58;

  --wpp-success-color-200: #E0FAEA;
  --wpp-success-color-400: #15803D;
  --wpp-success-color-500: #11652F;

  /* additional palette colors */
  --wpp-dataviz-color-cat-dark-1: #8508E8;
  --wpp-dataviz-color-cat-dark-2: #934074;
  --wpp-dataviz-color-cat-dark-3: #B91C1C;
  --wpp-dataviz-color-cat-dark-4: #C2410C;
  --wpp-dataviz-color-cat-dark-5: #A16207;
  --wpp-dataviz-color-cat-dark-6: #4D7C0F;
  --wpp-dataviz-color-cat-dark-7: #15803D;
  --wpp-dataviz-color-cat-dark-8: #0E7490;
  --wpp-dataviz-color-cat-dark-9: #4F4FBD;
  --wpp-dataviz-color-cat-dark-10: #565C60;

  --wpp-dataviz-color-cat-neutral-1: #BC71FB;
  --wpp-dataviz-color-cat-neutral-2: #C880B2;
  --wpp-dataviz-color-cat-neutral-3: #F87171;
  --wpp-dataviz-color-cat-neutral-4: #F48052;
  --wpp-dataviz-color-cat-neutral-5: #EAB308;
  --wpp-dataviz-color-cat-neutral-6: #68A512;
  --wpp-dataviz-color-cat-neutral-7: #1FBC59;
  --wpp-dataviz-color-cat-neutral-8: #06B6D4;
  --wpp-dataviz-color-cat-neutral-9: #7B90DF;
  --wpp-dataviz-color-cat-neutral-10: #8F979D;

  --wpp-dataviz-color-cat-light-1: #EED9FE;
  --wpp-dataviz-color-cat-light-2: #EDD6EB;
  --wpp-dataviz-color-cat-light-3: #FBE4E4;
  --wpp-dataviz-color-cat-light-4: #FDEAE2;
  --wpp-dataviz-color-cat-light-5: #FEF9C3;
  --wpp-dataviz-color-cat-light-6: #D9F99D;
  --wpp-dataviz-color-cat-light-7: #C2F5D4;
  --wpp-dataviz-color-cat-light-8: #A5F3FC;
  --wpp-dataviz-color-cat-light-9: #CCDCF8;
  --wpp-dataviz-color-cat-light-10: #D9DCDE;

  /* dataviz seq palette colors */
  --wpp-dataviz-color-seq-brand-100: #F3E6FE;
  --wpp-dataviz-color-seq-brand-200: #E6C9FD;
  --wpp-dataviz-color-seq-brand-300: #CE97FB;
  --wpp-dataviz-color-seq-brand-400: #B561FA;
  --wpp-dataviz-color-seq-brand-500: #9E30F8;
  --wpp-dataviz-color-seq-brand-600: #8508E8;
  --wpp-dataviz-color-seq-brand-700: #6A06BB;
  --wpp-dataviz-color-seq-brand-800: #4E058A;
  --wpp-dataviz-color-seq-brand-900: #35035E;
  --wpp-dataviz-color-seq-brand-1000: #19022C;

  --wpp-dataviz-color-seq-grey-100: #ECEDEE;
  --wpp-dataviz-color-seq-grey-200: #D9DCDE;
  --wpp-dataviz-color-seq-grey-300: #B6BBBF;
  --wpp-dataviz-color-seq-grey-400: #8F979D;
  --wpp-dataviz-color-seq-grey-500: #6D767D;
  --wpp-dataviz-color-seq-grey-600: #4D5358;
  --wpp-dataviz-color-seq-grey-700: #3E4347;
  --wpp-dataviz-color-seq-grey-800: #2D3134;
  --wpp-dataviz-color-seq-grey-900: #1F2123;
  --wpp-dataviz-color-seq-grey-1000: #0E0F10;

  --wpp-dataviz-color-seq-negative-100: #FBE4E4;
  --wpp-dataviz-color-seq-negative-200: #F7CACA;
  --wpp-dataviz-color-seq-negative-300: #F09999;
  --wpp-dataviz-color-seq-negative-400: #E86464;
  --wpp-dataviz-color-seq-negative-500: #DF2F2F;
  --wpp-dataviz-color-seq-negative-600: #B91C1C;
  --wpp-dataviz-color-seq-negative-700: #921616;
  --wpp-dataviz-color-seq-negative-800: #6F1111;
  --wpp-dataviz-color-seq-negative-900: #4B0B0B;
  --wpp-dataviz-color-seq-negative-1000: #230505;

  --wpp-dataviz-color-seq-positive-100: #E0FAEA;
  --wpp-dataviz-color-seq-positive-200: #C2F5D4;
  --wpp-dataviz-color-seq-positive-300: #84EBAA;
  --wpp-dataviz-color-seq-positive-400: #47E17F;
  --wpp-dataviz-color-seq-positive-500: #1FBC59;
  --wpp-dataviz-color-seq-positive-600: #15803D;
  --wpp-dataviz-color-seq-positive-700: #11652F;
  --wpp-dataviz-color-seq-positive-800: #0D4F25;
  --wpp-dataviz-color-seq-positive-900: #093519;
  --wpp-dataviz-color-seq-positive-1000: #041A0C;

  --wpp-dataviz-color-seq-warning-100: #FDEAE2;
  --wpp-dataviz-color-seq-warning-200: #FBD5C5;
  --wpp-dataviz-color-seq-warning-300: #F8AA8C;
  --wpp-dataviz-color-seq-warning-400: #F48052;
  --wpp-dataviz-color-seq-warning-500: #F15619;
  --wpp-dataviz-color-seq-warning-600: #C2410C;
  --wpp-dataviz-color-seq-warning-700: #9A320A;
  --wpp-dataviz-color-seq-warning-800: #732607;
  --wpp-dataviz-color-seq-warning-900: #4D1905;
  --wpp-dataviz-color-seq-warning-1000: #260D02;

  /* border */
  --wpp-border-radius-xs: 4px;
  --wpp-border-radius-s: 6px;
  --wpp-border-radius-m: 8px;
  --wpp-border-radius-l: 12px;
  --wpp-border-radius-xl: 20px;
  --wpp-border-radius-round: 64px;

  --wpp-border-width-s: 1px;
  --wpp-border-width-m: 2px;
  --wpp-border-width-l: 3px;

  --wpp-box-shadow-xs: 0px 1px 4px rgba(52, 58, 63, 0.12);
  --wpp-box-shadow-s: 0px 0px 1px rgba(52, 58, 63, 0.1), 0px 1px 5px rgba(52, 58, 63, 0.1);
  --wpp-box-shadow-m: 0px 1px 4px rgba(52, 58, 63, 0.05), 0px 4px 12px rgba(52, 58, 63, 0.12);
  --wpp-box-shadow-m-brand: 0px 4px 12px 0px rgba(94, 0, 181, 0.3);
  --wpp-box-shadow-l: 0px 8px 32px 0px rgba(52, 58, 63, 0.05);

  /* icons default colors */

  --wpp-icon-color-disabled: var(--wpp-grey-color-400);
  --wpp-icon-color: var(--wpp-grey-color-600);
  --wpp-icon-color-hover: var(--wpp-grey-color-800);
  --wpp-icon-color-active: var(--wpp-grey-color-900);

  /* brand color */

  --wpp-brand-color-disabled: var(--wpp-primary-color-300);
  --wpp-brand-color-hover: var(--wpp-primary-color-400);
  --wpp-brand-color: var(--wpp-primary-color-500);
  --wpp-brand-color-active: var(--wpp-primary-color-600);

  /* text color */

  --wpp-white-color: var(--wpp-grey-color-000);
  --wpp-text-color-white-info: var(--wpp-grey-color-200);
  --wpp-text-color-disabled: var(--wpp-grey-color-500);
  --wpp-text-color: var(--wpp-grey-color-1000);

  /* text system color */

  --wpp-text-color-danger: var(--wpp-danger-color-500);
  --wpp-text-color-warning: var(--wpp-warning-color-500);
  --wpp-text-color-success: var(--wpp-success-color-500);
  --wpp-text-color-info: var(--wpp-grey-color-800);

  /* surface and overlay color */

  --wpp-surface-bg-color: var(--wpp-grey-color-100);
  --wpp-overlay-bg-color: rgba(77, 83, 88, 0.6);
}
